            </div>
            <p class="ndbai">
Năm 1954, hòa bình lập lại trên nửa nước. Đồng bào các địa phương có sản vật gì quý đều gửi một ít lên biếu Bác để giới thiệu thành tích tăng gia với Người. Trong một bữa cơm, Bác thấy có món cà Nghệ, quả nhỏ, cùi giòn, ngâm mắm. Bữa ấy Bác ăn rất ngon. Hôm sau lại có món cà quê hương. Bác ăn hết vài ba quả. Thấy thế đồng chí anh nuôi lại dọn cà.
Bác hỏi: <br>
- Cà muối mua hay ai cho thế <br>
- Thưa Bác, đồng bào Nghệ An đưa ra tặng Bác. <br>
- Có nhiều không? <br>
- Dạ? Một ô tô ạ. <br>
Bác chậm rãi nói: <br>
- Thế này nhé. Cà Nghệ ăn rất ngon. Bác ăn mấy bữa liền là đủ rồi. Bây giờ ai muốn ăn nữa thì trả tiền cho đồng bào Nghệ An.

            </p>
            <hr>
            <div class="bai">                Bài 2:
ĐẢNG CỘNG SẢN VIỆT NAM, CHỦ TỊCH HỒ CHÍ MINH
TỔ CHỨC, LÃNH ĐẠO, GIÁO DỤC VÀ RÈN LUYỆN
QUÂN ĐỘI NHÂN DÂN VIỆT NAM</div>
            <div class="ndbai">


<strong>MỤC ĐÍCH, YÊU CẦU <br>
</strong>
<br>- Giáo dục cho chiến sĩ nhận thức sâu sắc công lao to lớn của Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh đối với quá trình xây dựng, trưởng thành và chiến thắng của quân đội ta; củng cố niềm tin vững chắc, niềm tự hào, lòng trung thành tuyệt đối với Đảng, Tổ quốc, nhân dân và chế độ xã hội chủ nghĩa.
<br>- Nêu cao trách nhiệm bảo vệ Đảng, Tổ quốc, nhân dân và chế độ xã hội chủ nghĩa; kiên quyết đấu tranh phản bác những quan điểm, tư tưởng sai trái, phản động chống phá Đảng, Nhà nước, Quân đội, hoàn thành tốt nhiệm vụ được giao.
<strong>
  <br><br>  NỘI DUNG<br>
<br>
I. ĐẢNG CỘNG SẢN VIỆT NAM, CHỦ TỊCH HỒ CHÍ MINH QUYẾT ĐỊNH SỰ TRƯỞNG THÀNH VÀ CHIẾN THẮNG CỦA QUÂN ĐỘI NHÂN DÂN VIỆT NAM
<br>1. Quá trình Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh tổ chức, lãnh đạo, giáo dục, rèn luyện Quân đội nhân dân Việt Nam 

</strong>
<br>Một là, Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh định ra đường lối, chủ trương tổ chức, xây dựng lực lượng vũ trang cách mạng.
<br>- Đảng Cộng sản Việt Nam ra đời vào ngày 3 tháng 2 năm 1930, trên cơ sở hợp nhất ba tổ chức Cộng sản ở Việt Nam là Đông Dương Cộng sản Đảng, An Nam Cộng sản Đảng và Đông Dương Cộng sản Liên đoàn.
<br>- Để lãnh đạo cách mạng đi đến thắng lợi, Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh đã định ra đường lối chính trị, quân sự đúng đắn. Trong đó, Đảng ta và Chủ tịch Hồ Chí Minh coi trọng, chăm lo tổ chức, xây dựng các lực lượng vũ trang nhân dân làm nòng cốt cho sự nghiệp đấu tranh giải phóng dân tộc, bảo vệ Tổ quốc. 
<br>- Trong cao trào Xô - viết Nghệ Tĩnh 1930-1931, Đảng ta đã tổ chức và lãnh đạo các đội Tự vệ Đỏ, Xích vệ Đỏ để cùng nhân dân đấu tranh giành chính quyền, thành lập các tổ chức công nông, bảo vệ thành quả cách mạng. 
<br>- Trong quá trình chuẩn bị lực lượng, tiến hành tổng khởi nghĩa giành chính quyền, Đảng ta và Chủ tịch Hồ Chí Minh đã lãnh đạo, thành lập các tổ chức vũ trang như: Du kích Nam Kỳ (1940), Đội du kích Bắc Sơn (1941), Cứu quốc quân (1941)… làm nòng cốt cho phong trào đấu tranh giải phóng dân tộc.
Hai là, Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh sáng lập và xây dựng Quân đội nhân dân Việt Nam.
<br>- Để tiếp tục xây dựng, phát triển lực lượng vũ trang cách mạng, chuẩn bị tổng khởi nghĩa, tháng 12 năm 1944, Chủ tịch Hồ Chí Minh ra Chỉ thị thành lập Đội Việt Nam Tuyên truyền giải phóng quân. 
Chỉ thị nhấn mạnh: “1. Tên, Đội Việt Nam Tuyên truyền Giải phóng quân, nghĩa là chính trị trọng hơn quân sự. Nó là đội tuyên truyền…, nguyên tắc chính là nguyên tắc tập trung lực lượng, cho nên, theo chỉ thị mới của Đoàn thể sẽ chọn lọc trong hàng ngũ những đội du kích Cao - Bắc - Lạng số cán bộ và đội viên kiên quyết, hăng hái nhất và sẽ tập trung một phần lớn vũ khí để lập ra đội chủ lực ”… 3. Về chiến thuật: Vận dụng lối đánh du kích, bí mật, nhanh chóng, tích cực, nay Đông mai Tây, lai vô ảnh, khứ vô tung”…, “Tuy lúc đầu qui mô của nó còn nhỏ, nhưng tiền đồ của nó rất vẻ vang. Nó là khởi điểm của giải phóng quân, nó có thể đi suốt từ Nam chí Bắc, khắp đất nước Việt Nam” .
<br>- Ngày 22 tháng 12 năm 1944, theo Chỉ thị của Chủ tịch Hồ Chí Minh, Đội Việt Nam tuyên truyền giải phóng quân được thành lập. Đồng chí Võ Nguyên Giáp được Chủ tịch Hồ Chí Minh ủy nhiệm lãnh đạo, đội biên chế thành 3 tiểu đội do đồng chí Hoàng Sâm làm Đội trưởng và đồng chí Xích Thắng làm Chính trị viên. Đội Việt Nam tuyên truyền giải phóng quân là đội quân chủ lực đầu tiên của Quân đội nhân dân Việt Nam. Ngày 22 tháng 12 năm 1944 đánh dấu sự ra đời của một tổ chức quân sự mới của dân tộc Việt Nam. Ngày đó được xác định là ngày thành lập Quân đội nhân dân Việt Nam. Ngày 22 tháng 12 hằng năm còn được xác định là ngày Hội Quốc phòng toàn dân .
<br>- Ngay sau khi Đội Việt Nam tuyên truyền giải phóng quân được thành lập, Đảng ta, Chủ tịch Hồ Chí Minh đã lãnh đạo quân đội ta phát triển nhanh chóng, không ngừng lớn mạnh, trưởng thành, làm nòng cốt trong đấu tranh vũ trang của toàn dân, hỗ trợ đắc lực cho đấu tranh chính trị của quần chúng, góp phần xứng đáng vào thắng lợi của Cách mạng Tháng Tám năm 1945, lập nên nước Việt Nam dân chủ cộng hoà, Nhà nước công nông đầu tiên ở Đông Nam Á.
Ba là, Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh lãnh đạo, giáo dục, rèn luyện quân đội ta trở thành lực lượng nòng cốt cùng toàn dân đấu tranh giải phóng dân tộc, thống nhất đất nước.
<br>- Từ năm 1945 đến năm 1954, Đảng ta và Chủ tịch Hồ Chí Minh tiếp tục lãnh đạo, xây dựng quân đội ta ngày càng lớn mạnh, cùng toàn dân làm nòng cốt bảo vệ thành quả của cách mạng và đánh thắng thực dân Pháp xâm lược.
<br>- Từ năm 1954 đến năm 1975, dưới sự lãnh đạo của Đảng, quân đội ta cùng toàn Đảng, toàn dân đánh thắng cuộc chiến tranh xâm lược tàn bạo của đế quốc Mỹ, hoàn thành sự nghiệp giải phóng dân tộc, thống nhất Tổ quốc, cả nước đi lên chủ nghĩa xã hội.
Bốn là, Đảng đã lãnh đạo, giáo dục, rèn luyện Quân đội nhân dân Việt Nam cách mạng, chính quy, tinh nhuệ, từng bước hiện đại, thực hiện thắng lợi nhiệm vụ bảo vệ Tổ quốc, góp phần quan trọng vào sự nghiệp xây dựng chủ nghĩa xã hội và làm tròn nghĩa vụ quốc tế cao cả.
 <br>- Trong giai đoạn mới của cách mạng, Đảng ta thường xuyên quan tâm lãnh đạo xây dựng quân đội vững mạnh về mọi mặt, thực sự là công cụ bạo lực sắc bén, lực lượng chính trị tin cậy, trung thành của Đảng và Nhà nước; quân đội ta đã cùng với toàn Đảng, toàn dân bảo vệ vững chắc độc lập, chủ quyền, thống nhất và toàn vẹn lãnh thổ của Tổ quốc và làm tròn nghĩa vụ quốc tế.
<br>- Dưới sự lãnh đạo của Đảng, quân đội ta cùng với các tổ chức, các lực lượng đấu tranh làm thất bại âm mưu, thủ đoạn “diễn biến hòa bình”, bạo loạn lật đổ của các thế lực thù địch, bảo vệ những thành quả của cách mạng, bảo vệ Đảng, Nhà nước, nhân dân, bảo vệ chế độ xã hội chủ nghĩa.
<br>- Trong thời kỳ mới, cùng với làm tốt nhiệm vụ huấn luyện, sẵn sàng chiến đấu và chiến đấu, quân đội ta còn tích cực tham gia các chương trình xóa đói, giảm nghèo, phát triển kinh tế - xã hội của đất nước; là lực lượng đi đầu, nòng cốt trong tham gia phòng, chống thiên tai, dịch bệnh, cứu hộ, cứu nạn, khắc phục hậu quả sau chiến tranh; tham gia hoạt động gìn giữ hòa bình của Liên hợp quốc…
<strong><br>	2. Sự lãnh đạo, giáo dục, rèn luyện của Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh là nhân tố quyết định sự trưởng thành và chiến thắng của quân đội ta
</strong>
<br>- Sự lãnh đạo, giáo dục, rèn luyện của Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh là nhân tố quyết định bản chất giai cấp, mục tiêu, lý tưởng chiến đấu của quân đội.
Quân đội ta do Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh sáng lập, lãnh đạo, rèn luyện, xây dựng theo bản chất cách mạng của giai cấp công nhân Việt Nam. 
Mục tiêu lý tưởng của Đảng cũng là mục tiêu lý tưởng chiến đấu của quân đội. Đó là độc lập dân tộc gắn liền với chủ nghĩa xã hội, vì mục tiêu “dân giàu, nước mạnh, dân chủ, công bằng, văn minh”. 
Dưới sự lãnh đạo giáo dục, rèn luyện của Đảng và Chủ tịch Hồ Chí Minh đã tôi luyện quân đội ta luôn luôn là lực lượng chính trị, đội quân chiến đấu tuyệt đối trung thành tin cậy của Đảng, của Nhà nước và nhân dân.
<br>- Sự lãnh đạo, giáo dục, rèn luyện của Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh là nhân tố quyết định nguyên tắc tổ chức, xây dựng và mọi hoạt động của quân đội.
 Đảng quyết định về cách thức tổ chức, phương hướng xây dựng, phương thức hoạt động của quân đội trong quá trình đấu tranh cách mạng. Quân đội ta được tổ chức theo nguyên tắc tổ chức của quân đội cách mạng, có hệ thống lãnh đạo, hệ thống chỉ huy, hệ thống cơ quan chính trị và cán bộ chính trị thống nhất; có cơ cấu hợp lý, được tổ chức chặt chẽ, đồng bộ, có bản lĩnh chính trị kiên định vững vàng, thống nhất cao về ý chí và hành động, có kỷ luật tự giác, nghiêm minh.
 Trong giai đoạn hiện nay, Đảng ta xác định xây dựng quân đội cách mạng, chính quy, tinh nhuệ, từng bước hiện đại, ưu tiên hiện đại hóa một số quân chủng, binh chủng, lực lượng, tạo tiền đề vững chắc để phấn đấu từ năm 2030 xây dựng quân đội hiện đại, thực hiện tốt vai trò nòng cốt trong xây dựng nền quốc phòng toàn dân; xây dựng lực lượng dự bị động viên hùng hậu với chất lượng ngày càng cao, dân quân tự vệ vững mạnh, rộng khắp. Toàn quân sẵn sàng chiến đấu cao và chiến đấu thắng lợi trong mọi tình huống, bảo vệ vững chắc Tổ quốc, góp phần giữ vững môi trường hòa bình, ổn định để xây dựng và phát triển đất nước. 
	<br>- Đảng định ra đường lối, khoa học - nghệ thuật quân sự và cách đánh của quân đội.
Quan điểm, đường lối, khoa học - nghệ thuật quân sự của Đảng là đường lối chiến tranh nhân dân và quốc phòng toàn dân. Quân đội ta luôn quán triệt sâu sắc đường lối, khoa học - nghệ thuật quân sự của Đảng làm cơ sở xây dựng phương thức tác chiến, quyết định cách đánh với từng đối tượng, quyết định việc sử dụng lực lượng và vũ khí, trang bị kỹ thuật quân sự. Do quán triệt và thực hiện nghiêm túc đường lối khoa học và nghệ thuật quân sự của Đảng, quân đội ta đã sáng tạo những cách đánh độc đáo, tạo nên sức mạnh to lớn để chiến thắng kẻ thù.
<br>- Thực tiễn lịch sử hơn 75 năm qua, dưới sự lãnh đạo, giáo dục, rèn luyện của Đảng và Chủ tịch Hồ Chí Minh, quân đội ta đã cùng với toàn dân vượt qua những chặng đường đầy gian lao, thử thách, giành thắng lợi vẻ vang, không ngừng lớn mạnh trưởng thành, cùng toàn dân đấu tranh giải phóng dân tộc, bảo vệ Tổ quốc và làm tròn nghĩa vụ quốc tế cao cả, với những chiến công và phần thưởng cao quý tiêu biểu là: 
Những chiến công tiêu biểu
<br>+ Mở đầu là diệt đồn Phai Khắt, Nà Ngần ngày 25 và 26 tháng 12 năm 1944. Tiếp đó, hưởng ứng Lời kêu gọi toàn quốc kháng chiến của Chủ tịch Hồ Chí Minh (19-12-1946), nhiều trận đánh quyết liệt đã diễn ra ở Bắc Bộ Phủ, nhà ga, cầu Long Biên, chợ Đồng Xuân (Hà Nội); chiến thắng Việt Bắc năm 1947; chiến thắng Biên Giới năm 1950; Chiến thắng lịch sử Điện Biên Phủ Ngày 7 tháng 5 năm 1954.
<br>+ Đánh bại cuộc chiến tranh phá hoại lần thứ nhất của đế quốc Mỹ ở miền Bắc (từ ngày 7-2-1965 đến ngày 1-11-1968); chiến thắng Bình Giã (từ ngày 2-12-1964 đến ngày 3-1-1965), đánh dấu sự thất bại của Mỹ - ngụy trong chiến lược “Chiến tranh đặc biệt”; chiến thắng Núi Thành (26-5-1965); Chiến thắng Vạn Tường (từ ngày 18 đến ngày 19-8-1965) đánh bại cuộc hành quân “Tìm diệt” quy mô lớn của quân viễn chinh Mỹ; chiến dịch Plây Me (từ ngày 19 tháng 10 đến ngày 26-11-1965); cuộc Tổng tiến công và nổi dậy Tết Mậu Thân 1968 ta đánh chiếm 39 mục tiêu quan trọng, làm chủ hầu hết thành phố và chốt giữ 25 ngày đêm; chiến dịch Đường 9 - Nam Lào (từ ngày 30-1 đến ngày 23-3-1971); đánh bại cuộc chiến tranh phá hoại lần thứ hai của đế quốc Mỹ (từ ngày 6-4-1972 đến ngày 15-1-1973); đại thắng mùa Xuân ngày 30 tháng 4 năm 1975 đất nước thống nhất, cả nước đi lên chủ nghĩa xã hội. 
<br>+ Chiến thắng chiến tranh bảo vệ biên giới Tây Nam của Tổ quốc và cùng quân dân Campuchia chiến thắng chế độ diệt chủng ngày 7 tháng 1 năm 1979; cuộc chiến đấu bảo vệ biên giới phía Bắc của Tổ quốc ngày 7 tháng 2 năm 1979.
 <br>+ Trong phòng, chống thiên tai, lũ lụt, dịch bệnh, nhiều cán bộ, chiến sĩ đã hy sinh xương máu để bảo vệ tài sản của nhân dân, của quân đội. Đặc biệt là những thành tích của quân đội trong tham gia phòng, chống  dịch COVID-19 được Đảng, Nhà nước và nhân dân ghi nhận, đánh giá cao.
<br>+ Tham gia hoạt động gìn giữ hòa bình của Liên hợp quốc, góp phần thúc đẩy hợp tác quốc phòng đa phương, giữ vững niềm tin của Liên hợp quốc và bạn bè quốc tế, góp phần tăng cường mối quan hệ hợp tác quốc phòng với nhiều quốc gia, đối tác, nâng cao vị thế, uy tín của Việt Nam trên trường quốc tế.
Những Phần thưởng cao quý Đảng, Nhà nước trao tặng
Quân đội nhân dân Việt Nam đã 5 lần được Nhà nước tặng thưởng Huân chương Sao vàng (Huân chương cao quý nhất của Nhà nước ta) vào các năm 1974, 1979, 1984, 1999 và năm 2014; Huân chương Lao động hạng Nhất vào các năm 2015, 2019; Huân chương Quân công hạng nhất, năm 2019. Nhà nước Cộng hòa dân chủ nhân dân Lào tặng thưởng Quân đội nhân dân Việt Nam Huân chương Sao vàng quốc gia (Huân chương cao quý nhất của Nhà nước Lào) ngày 18 tháng 12 năm 1984. Nhà nước Cộng hòa nhân dân Campuchia tặng thưởng Quân đội nhân dân Việt Nam Huân chương Ăng-co (Huân chương cao quý nhất của Nhà nước Campuchia) ngày 19 tháng 12 năm 1984. Cùng nhiều tập thể, cá nhân được tặng thưởng Huân chương Sao vàng; Huân chương Hồ Chí Minh; danh hiệu Anh hùng lực lượng vũ trang nhân dân; Anh hùng Lao động; Huân chương Độc lập; Huân chương Quân công, Huân chương Chiến công. Hàng vạn cán bộ, chiến sĩ được tặng thưởng huân chương, huy chương các loại.
<br>Trong thực hiện nhiệm vụ quốc tế cao cả, đã có hàng trăm đơn vị, hàng chục nghìn cán bộ, chiến sĩ quân tình nguyện Việt Nam được Nhà nước Cộng hòa dân chủ nhân dân Lào tặng thưởng Huân chương Tự do, Huân chương Anh dũng, Huân chương Lao động, Huân chương Hữu nghị và Nhà nước Cộng hòa nhân dân Campuchia tặng thưởng Huân chương Ăng-co, Huân chương Bảo vệ Tổ quốc vì đã lập được thành tích đặc biệt xuất sắc trong nhiệm vụ chiến đấu và công tác giúp bạn. 
Những thành tích, chiến công, sự trưởng thành, lớn mạnh của quân đội ta trong hơn 75 năm qua gắn liền với sự lãnh đạo tài tình, sáng suốt của Đảng và công ơn giáo dục, rèn luyện của Chủ tịch Hồ Chí Minh.
<strong>
    <br><br>II. GIỮ VỮNG VÀ TĂNG CƯỜNG SỰ LÃNH ĐẠO CỦA ĐẢNG ĐỐI VỚI QUÂN ĐỘI NHÂN DÂN VIỆT NAM<br><br>
<br>1. Sự cần thiết phải giữ vững và tăng cường sự lãnh đạo của Đảng đối với Quân đội nhân dân Việt Nam 
</strong>
<br>- Giữ vững và tăng cường sự lãnh đạo của Đảng đối với quân đội để quân đội ta luôn giữ vững và tăng cường bản chất cách mạng, kiên định mục tiêu, lý tưởng chiến đấu, chủ động phòng, chống những biểu hiện “tự diễn biến”, “tự chuyển hóa” trong nội bộ.
Quân đội ta do Đảng Cộng sản Việt Nam tổ chức, lãnh đạo, giáo dục và rèn luyện, luôn mang trong mình bản chất giai cấp công nhân, tính nhân dân và tính dân tộc sâu sắc, chiến đấu vì mục tiêu, lý tưởng của Đảng, vì độc lập dân tộc và chủ nghĩa xã hội, vì tự do, hạnh phúc của nhân dân. Sự lãnh đạo của Đảng đối với quân đội là nhân tố quyết định sự trưởng thành, chiến đấu và chiến thắng của quân đội. Nếu không giữ vững và tăng cường sự lãnh đạo của Đảng đối với quân đội, tất yếu quân đội sẽ mất phương hướng chiến đấu, phân rã về chính trị, tư tưởng, tổ chức, không còn sức mạnh chiến đấu để chiến thắng kẻ thù. Nên bất luận trong hoàn cảnh nào cũng luôn kiên định nguyên tắc Đảng lãnh đạo tuyệt đối, trực tiếp về mọi mặt đối với quân đội. 
<br>Hiện nay, những biểu hiện suy thoái về tư tưởng chính trị, đạo đức, lối sống, “tự diễn biến”, “tự chuyển hóa” đang diễn ra rất phức tạp, tác động ảnh hưởng trực tiếp đến sức mạnh chiến đấu của quân đội. Những biểu hiện như: ra chỉ thị, mệnh lệnh không đúng, chấp hành chỉ thị, mệnh lệnh không nghiêm; nói, làm trái nghị quyết, chỉ thị; lười học tập chính trị, thiếu niềm tin; mất đoàn kết; vi phạm nguyên tắc tập trung dân chủ; vi phạm pháp luật Nhà nước, kỷ luật quân đội; thờ ơ, vô cảm trước những khó khăn, bức xúc của bộ đội và nhân dân; tham nhũng, lãng phí; làm lộ bí mật quân sự, quốc gia; thiếu gương mẫu, biểu hiện chủ nghĩa cá nhân… cần phải được lãnh đạo, chỉ đạo khắc phục triệt để để quân đội làm tròn nhiệm vụ bảo vệ Tổ quốc trong tình hình mới.
<br>- Đảng phải nắm chắc quân đội để quân đội ta luôn là công cụ bạo lực sắc bén, lực lượng chính trị, lực lượng chiến đấu tuyệt đối trung thành, tin cậy của Đảng, Nhà nước và nhân dân, chống âm mưu “phi chính trị hoá”, “dân sự hóa” quân đội của các thế lực thù địch.
<br>Trong giai đoạn hiện nay, các thế lực thù địch tăng cường các hoạt động “diễn biến hòa bình”, bạo loạn lật đổ, câu kết với các đối tượng phản động, cơ hội chính trị hòng xóa bỏ vai trò lãnh đạo của Đảng Cộng sản Việt Nam, xóa bỏ chế độ xã hội chủ nghĩa ở nước ta. 
<br>Các thế lực thù địch tìm mọi cách đòi “phi chính trị hóa” quân đội, kêu gọi quân đội đứng ngoài chính trị, chỉ bảo vệ đất nước và nhân dân, như đòi đa nguyên chính trị, đa đảng đối lập hòng chia quyền lãnh đạo của Đảng Cộng sản Việt Nam đối với quân đội. Chúng ra sức xuyên tạc nhằm hạ thấp uy tín của Đảng, tách quân đội khỏi sự lãnh đạo của Đảng để vô hiệu hóa quân đội, làm cho quân đội phai nhạt mục tiêu, lý tưởng chiến đấu. Do đó, để góp phần làm thất bại mọi âm mưu, thủ đoạn chống phá của các thế lực thù địch, càng phải tăng cường sự lãnh đạo của Đảng đối với quân đội.  
<br>- Giữ vững và tăng cường sự lãnh đạo của Đảng đối với quân đội nhằm làm cho quân đội ta không ngừng trưởng thành và vững mạnh toàn diện, hoàn thành thắng lợi mọi nhiệm vụ được giao.
Đại hội XIII của Đảng xác định chủ trương, đường lối xây dựng Quân đội nhân dân “cách mạng, chính quy, tinh nhuệ, từng bước hiện đại, trong đó có một số quân chủng, binh chủng, lực lượng tiến thẳng lên hiện đại; vững mạnh về chính trị, có chất lượng tổng hợp và có sức mạnh chiến đấu cao” . Quán triệt quan điểm, đường lối của Đảng về xây dựng quân đội và yêu cầu, nhiệm vụ bảo vệ Tổ quốc trong tình hình mới, đòi hỏi phải tiếp tục giữ vững và tăng cường vai trò lãnh đạo tuyệt đối, trực tiếp về mọi mặt của Đảng đối với quân đội. 
<strong><br>2. Nội dung giữ vững và tăng cường sự lãnh đạo của Đảng đối với Quân đội nhân dân Việt Nam
</strong>
<br>- Nhận thức đúng, có niềm tin vững chắc và kiên quyết đấu tranh bảo vệ chủ nghĩa Mác - Lênin, tư tưởng Hồ Chí Minh, đường lối, quan điểm của Đảng; bảo vệ nền tảng tư tưởng của Đảng trong quân đội.  
Chủ nghĩa Mác - Lênin, tư tưởng Hồ Chí Minh là một hệ thống quan điểm tư tưởng cách mạng, khoa học, đã trở thành kim chỉ nam cho mọi hoạt động của Đảng, của nhân dân ta. Quân đội ta là quân đội cách mạng của Đảng, do vậy quân đội phải thấm nhuần chủ nghĩa Mác - Lênin, tư tưởng Hồ Chí Minh, đường lối, quan điểm của Đảng. 
Quân đội phải tích cực nghiên cứu, học tập, vận dụng sáng tạo chủ nghĩa Mác - Lênin, tư tưởng Hồ Chí Minh vào quá trình xây dựng quân đội “cách mạng, chính quy, tinh nhuệ, từng bước hiện đại”; vào huấn luyện, sẵn sàng chiến đấu, công tác, lao động sản xuất, vào các hoạt động đời sống văn hoá, tinh thần ở từng đơn vị. 
Chống mọi biểu hiện mơ hồ, mất cảnh giác trước những luận điệu xuyên tạc của các thế lực thù địch; kiên quyết đấu tranh bảo vệ chủ nghĩa Mác - Lênin, tư tưởng Hồ Chí Minh; bảo vệ đường lối, quan điểm của Đảng.
<br>- Giữ vững nguyên tắc Đảng lãnh đạo tuyệt đối, trực tiếp về mọi mặt đối với quân đội ta; các cấp uỷ đảng trong quân đội lãnh đạo đơn vị về mọi mặt.
Đảng Cộng sản Việt Nam là lực lượng duy nhất lãnh đạo quân đội, mọi tổ chức, hoạt động của quân đội đều có tổ chức đảng trực tiếp lãnh đạo mọi mặt, ở đâu có bộ đội là ở đó có sự lãnh đạo của Đảng. 
Cán bộ, chiến sĩ thực hiện nghiêm nguyên tắc tổ chức của Đảng; quy chế, quy định và nền nếp, chế độ hoạt động của tổ chức cơ sở đảng; bảo đảm sự lãnh đạo của Đảng được thông suốt từ Trung ương tới từng chi bộ, từng cán bộ, đảng viên, quần chúng trong quân đội. 
Thực hiện nghiêm chế độ một người chỉ huy gắn với thực hiện chế độ chính ủy, chính trị viên trong quân đội và các bộ phận hợp thành cơ chế đó (hệ thống chỉ huy, hệ thống tổ chức đảng, hệ thống cơ quan chính trị và cán bộ chính trị các cấp, hệ thống tổ chức quần chúng).
Nâng cao chất lượng, hiệu quả hoạt động của cơ quan chính trị các cấp và chính ủy, chính trị viên.
Mọi hoạt động của đơn vị phải trên cơ sở quán triệt, thực hiện các chỉ thị, nghị quyết của Đảng, mệnh lệnh, chỉ thị của cấp trên. Cá nhân phải phục tùng tổ chức, cấp dưới phải phục tùng mệnh lệnh của cấp trên.
<br>- Xây dựng tổ chức đảng và đội ngũ cán bộ, đảng viên thực sự vững mạnh, đáp ứng với yêu cầu nhiệm vụ.
Xây dựng cấp ủy, các tổ chức đảng trong sạch, vững mạnh tiêu biểu, làm cơ sở xây dựng đơn vị và các tổ chức chỉ huy, tổ chức quần chúng vững mạnh; chăm lo xây dựng đội ngũ cán bộ, đảng viên cả về phẩm chất và năng lực đáp ứng yêu cầu nhiệm vụ trong giai đoạn mới. 
Giải quyết tốt mối quan hệ giữa lãnh đạo với chỉ huy, giữa tổ chức đảng với các tổ chức quần chúng và đơn vị, thực sự đoàn kết nhất trí cao giữa đảng viên với quần chúng, cán bộ với chiến sĩ, giữa các tổ chức trong đơn vị.
Đẩy mạnh học tập và làm theo tư tưởng, đạo đức, phong cách Hồ Chí Minh thành việc làm tự giác, thường xuyên trong các tổ chức đảng, tổ chức quần chúng và toàn đơn vị. 
<strong><br>3. Trách nhiệm của chiến sĩ góp phần giữ vững và tăng cường sự lãnh đạo của Đảng đối với quân đội
</strong>
<br>- Tin tưởng tuyệt đối vào sự lãnh đạo của Đảng, chế độ xã hội chủ nghĩa vào thắng lợi của công cuộc đổi mới đất nước và sức mạnh chiến đấu, chiến thắng của quân và dân ta. Kiên định mục tiêu, lý tưởng chiến đấu vì độc lập dân tộc và chủ nghĩa xã hội, vì hạnh phúc của nhân dân.
Tin tưởng và biết ơn Đảng, Bác Hồ kính yêu đã lãnh đạo nhân dân đấu tranh giành độc lập dân tộc và xây dựng cuộc sống tốt đẹp như ngày nay, từ đó, tuyệt đối trung thành với Tổ quốc, với Đảng, Nhà nước và nhân dân. 
Nắm vững và nhất trí cao với nghị quyết của các cấp ủy đảng, trực tiếp là chi bộ; tin tưởng và chấp hành nghiêm túc điều lệnh, điều lệ quân đội, chỉ thị, mệnh lệnh của người chỉ huy; không ngừng tu dưỡng phẩm chất đạo đức, lối sống của người quân nhân cách mạng, hoàn thành tốt chức trách, nhiệm vụ được giao.
Yêu nước phải gắn liền với yêu Đảng, gắn bó máu thịt với nhân dân, yêu chế độ xã hội chủ nghĩa; yêu thương đồng chí, đồng đội, gắn bó yêu mến đơn vị.
<br>- Ra sức học tập, quán triệt sâu sắc các nghị quyết, chỉ thị của Đảng Cộng sản Việt Nam, của Quân uỷ Trung ương và Bộ Quốc phòng; chủ trương, biện pháp lãnh đạo của chi bộ và cấp ủy đảng các cấp; mục tiêu, yêu cầu, nhiệm vụ của đơn vị, làm cơ sở nâng cao bản lĩnh chính trị, trình độ sẵn sàng chiến đấu, hoàn thành mọi nhiệm vụ được giao.
Thường xuyên quán triệt, nhận thức đúng đắn về đường lối, quan điểm của Đảng; chủ trương, biện pháp lãnh đạo của cấp ủy đảng các cấp và mục tiêu, yêu cầu, nhiệm vụ của quân đội, của đơn vị là cơ sở bảo đảm sự định hướng tư tưởng, xây dựng bản lĩnh chính trị kiên định, vững vàng của mỗi quân nhân. 
Luôn luôn đề cao tinh thần tự giác trong học tập, công tác, huấn luyện, chiến đấu; không ngừng nâng cao tri thức về khoa học - kỹ thuật quân sự, tinh thông về kỹ thuật, chiến thuật, chuyên môn nghiệp vụ và có kiến thức về kinh tế, văn hóa, pháp luật cần thiết để hoàn thành chức trách, nhiệm vụ được giao.
<br>- Tích cực học tập và làm theo tư tưởng, đạo đức, phong cách Hồ Chí Minh, góp phần xây dựng chi bộ và các tổ chức đảng trong sạch vững mạnh tiêu biểu, đơn vị vững mạnh toàn diện “Mẫu mực, tiêu biểu”, phấn đấu trở thành người quân nhân ưu tú, người công dân kiểu mẫu trong sự nghiệp xây dựng và bảo vệ Tổ quốc. 
Mỗi quân nhân cần tự giác tu dưỡng, rèn luyện bản lĩnh chính trị, ý thức trách nhiệm, rèn luyện đạo đức cách mạng, không ngừng nâng cao trình độ, năng lực, phương pháp tác phong công tác, tăng cường đoàn kết, giữ nghiêm kỷ luật. Xây dựng lối sống khiêm tốn, giản dị, trong sáng, biết tôn trọng và bảo vệ cái đúng; có tình thương yêu với đồng chí, đồng đội, đoàn kết giúp đỡ nhau cùng tiến bộ. 
Luôn phát huy dân chủ, đề cao ý thức tổ chức kỷ luật, chấp hành nghiêm mệnh lệnh, chỉ thị của cấp trên; kiên quyết đấu tranh với mọi biểu hiện tiêu cực ảnh hưởng đến quân đội, đơn vị, trái với nghị quyết, chỉ thị của các cấp.
Tiếp tục thực hiện tốt chỉ thị số 855-CT/QUTW ngày 12 tháng 8 năm 2019 về đẩy mạnh thực hiện Cuộc vận động phát huy truyền thống, cống hiến tài năng, xứng danh “Bộ đội Cụ Hồ” thời kỳ mới, Cuộc vận động “Xây dựng môi trường văn hoá tốt đẹp, lành mạnh, phong phú trong quân đội”, gắn với phong trào “Toàn dân đoàn kết xây dựng đời sống văn hóa”. 
<br>- Tích cực tham gia xây dựng Đảng, xây dựng chi bộ trong sạch vững mạnh, bảo vệ đường lối, chủ trương, sự đoàn kết thống nhất, uy tín của Đảng, đấu tranh chống các quan điểm, tư tưởng, nhận thức và hành động sai trái, thù địch.
Mỗi chiến sĩ phải phát huy dân chủ, chủ động, tích cực tham gia đóng góp ý kiến xây dựng Đảng, trực tiếp là tham gia góp ý phê bình cán bộ, đảng viên trong chi bộ, đảng bộ, tham gia xây dựng nghị quyết của Đảng. Khi tổ chức đảng đã có chủ trương, nghị quyết thì quán triệt và thực hiện nghiêm.
Không ngừng phấn đấu học tập, rèn luyện, nâng cao chất lượng, hiệu quả công tác, phấn đấu để được đứng vào hàng ngũ của Đảng.
Nêu cao cảnh giác cách mạng, kiên quyết đấu tranh, phản bác những luận điệu, thông tin sai trái, xuyên tạc, nói xấu Đảng, bôi nhọ cán bộ lãnh đạo Đảng, Nhà nước, quân đội; phê phán những nhận thức và hành vi sai trái, vi phạm kỷ luật Đảng, pháp luật Nhà nước, kỷ luật quân đội, quy định của đơn vị; xây dựng đơn vị vững mạnh toàn diện, nội bộ luôn đoàn kết thống nhất, thực hiện thắng lợi mọi nhiệm vụ được giao.


            </div>

  <hr>
            <div class="bai">Bài 3:
BẢN CHẤT, MỤC TIÊU LÝ TƯỞNG CHIẾN ĐẤU, 
CHỨC NĂNG, NHIỆM VỤ VÀ ĐỐI TƯỢNG TÁC CHIẾN 
CỦA QUÂN ĐỘI NHÂN DÂN VIỆT NAM HIÊN NAY
</div>
            <div class="ndbai">

<strong><br>
<br>
MỤC ĐÍCH, YÊU CẦU
<br>
<br>
</strong>
<br>- Giáo dục cho chiến sĩ nhận thức đúng về quân đội ta là quân đội cách mạng, mang bản chất giai cấp công nhân, có tính nhân dân và tính dân tộc sâu sắc; đồng thời, hiểu rõ mục tiêu, lý tưởng chiến đấu, chức năng, nhiệm vụ của Quân đội nhân dân Việt Nam.
<br>- Nâng cao nhận thức, đề cao trách nhiệm của mỗi chiến sĩ phấn đấu trở thành người quân nhân ưu tú của Quân đội nhân dân Việt Nam; kiên quyết đấu tranh, phản bác những quan điểm sai trái, phản động xuyên tạc truyền thống, bản chất, mục tiêu, lý tưởng chiến đấu và chức năng, nhiệm vụ của quân đội ta, nhất là chức năng lao động sản xuất của quân đội trong tình hình mới; sẵn sàng nhận và hoàn thành tốt mọi nhiệm vụ được giao.
<strong>
    <br><br>NỘI DUNG <br>
<br><br>I. BẢN CHẤT, MỤC TIÊU LÝ TƯỞNG CHIẾN ĐẤU CỦA QUÂN ĐỘI NHÂN DÂN VIỆT NAM
<strong><br>1. Bản chất của Quân đội nhân dân Việt Nam
</strong>
</strong>
<br>Quân đội nhân dân Việt Nam - đội quân cách mạng, mang bản chất giai cấp công nhân, có tính nhân dân và tính dân tộc sâu sắc. 
<br>- Trong lịch sử phát triển của xã hội loài người, quân đội hình thành cùng với sự phân chia giai cấp, đấu tranh giai cấp và xuất hiện nhà nước của giai cấp thống trị. Quân đội do giai cấp, nhà nước tổ chức ra làm công cụ bạo lực để bảo vệ lợi ích cho giai cấp, nhà nước. Nên quân đội mang bản chất của giai cấp, nhà nước tổ chức ra nó. 
<br>- Quân đội nhân dân Việt Nam do Đảng Cộng sản Việt Nam và Chủ tịch Hồ Chí Minh tổ chức, lãnh đạo, giáo dục và rèn luyện, nên quân đội ta mang bản chất giai cấp công nhân Việt Nam; cán bộ, chiến sĩ trong Quân đội nhân dân Việt Nam là con em các tầng lớp nhân dân trong cộng đồng các dân tộc Việt Nam, được nhân dân thương yêu, đùm bọc, nuôi dưỡng, chiến đấu để bảo vệ độc lập, chủ quyền, thống nhất, toàn vẹn lãnh thổ của Tổ quốc, bảo vệ Đảng, Nhà nước, nhân dân và chế độ xã hội chủ nghĩa. Do đó, quân đội ta có tính nhân dân và tính dân tộc sâu sắc. 
<br>- Mục tiêu, lý tưởng chiến đấu của Quân đội nhân dân Việt Nam cũng là mục tiêu, lý tưởng của Đảng Cộng sản Việt Nam, của giai cấp công nhân Việt Nam và của dân tộc, của nhân dân Việt Nam. Do đó, Quân đội nhân dân Việt Nam là quân đội của nhân dân, do nhân dân và vì nhân dân, mang bản chất giai cấp công nhân, có tính nhân dân và tính dân tộc sâu sắc. 
<strong><br>2. Mục tiêu, lý tưởng chiến đấu của Quân đội nhân dân Việt Nam 
</strong>
<br>Mục tiêu, lý tưởng chiến đấu của quân đội ta là: Vì độc lập dân tộc và chủ nghĩa xã hội, vì mục tiêu “dân giàu, nước mạnh, dân chủ, công bằng, văn minh”.
<br>- Vì sao?
<br>+ Quân đội ta chiến đấu vì mục tiêu, lý tưởng của Đảng Cộng sản Việt Nam, của giai cấp công nhân và lợi ích của dân tộc, của nhân dân. Ngoài mục tiêu, lý tưởng đó, quân đội ta không có mục tiêu, lý tưởng nào khác.
<br>+ Mục tiêu, lý tưởng chiến đấu vì độc lập dân tộc và chủ nghĩa xã hội là sự phản ánh sâu sắc, tập trung nhất bản chất giai cấp công nhân, tính nhân dân, tính dân tộc của quân đội ta; khác về chất so với mục tiêu chiến đấu của quân đội của giai cấp tư sản. Quân đội tư sản chiến đấu để nhằm thực hiện đường lối đối nội, đối ngoại phản động của giai cấp bóc lột, chống lại nhân dân lao động trong nước và xâm lược, nô dịch các dân tộc khác.
<br>+ Mục tiêu, lý tưởng chiến đấu của quân đội được cụ thể hóa thành nhiệm vụ chính trị của quân đội trong từng thời kỳ, giai đoạn cách mạng; là định hướng chính trị cho nhận thức và hành động; đồng thời, là động lực thúc đẩy mọi cán bộ, chiến sĩ phấn đấu, rèn luyện, xây dựng ý chí, quyết tâm thực hiện thắng lợi mọi nhiệm vụ được giao.
<br>- Nội dung biểu hiện:
<br>+ Tuyệt đối trung thành với Tổ quốc, với Đảng, Nhà nước và nhân dân. Quân đội ta luôn quán triệt và thực hiện nghiêm chỉ thị, nghị quyết của Đảng, pháp luật Nhà nước, sẵn sàng chiến đấu hy sinh vì độc lập tự do của Tổ quốc, vì hạnh phúc của nhân dân.
<br>+ Thực hiện tốt vai trò nòng cốt trong xây dựng nền quốc phòng toàn dân, thế trận quốc phòng toàn dân gắn với thế trận an ninh nhân dân vững chắc. Quân đội luôn phối hợp chặt chẽ với các ban, bộ, ngành Trung ương và các địa phương điều chỉnh thế bố trí chiến lược, huy động nguồn lực xây dựng khu vực phòng thủ, tăng cường tiềm lực quốc phòng của đất nước. 
Gắn bó máu thịt với nhân dân; tích cực tham gia xây dựng cơ sở chính trị địa phương vững mạnh, giúp dân phát triển kinh tế, văn hóa, xã hội, xóa đói, giảm nghèo; phối hợp xử lý các tình huống phức tạp, những địa bàn chiến lược, trọng yếu về quốc phòng, an ninh.
Quân đội ta luôn là lực lượng nòng cốt, xung kích đi đầu trong phòng, chống thiên tai, dịch bệnh, cứu hộ, cứu nạn, khắc phục sự cố môi trường, sẵn sàng hy sinh vì cuộc sống bình yên của nhân dân; là chỗ dựa vững chắc, tin cậy của Đảng, Nhà nước và nhân dân, xứng đáng là quân đội của nhân dân, quân đội trong lòng dân.
<br>+ Tích cực, chủ động đấu tranh chống âm mưu, thủ đoạn “diễn biến hòa bình”, thông tin xấu, độc của các thế lực thù địch phủ nhận mục tiêu, lý tưởng chiến đấu của quân đội ta. Các thế lực thù địch luôn luôn thực hiện âm mưu, thủ đoạn “diễn biến hòa bình”, “phi chính trị hóa”, “dân sự hóa” quân đội, làm cho quân đội ta mất phương hướng chính trị, tiến tới bị vô hiệu hóa; chúng tăng cường tuyên truyền, xuyên tạc, phủ nhận mục tiêu, lý tưởng chiến đấu của quân đội nhằm xóa bỏ vai trò lãnh đạo của Đảng đối với quân đội, làm cho cán bộ, chiến sĩ mất phương hướng chính trị, phai nhạt mục tiêu, lý tưởng chiến đấu. 
<br>Vì vậy, quân đội luôn phải tích cực, chủ động đấu tranh trực diện với các thế lực thù địch trên mặt trận chính trị, tư tưởng nhằm bảo vệ và phát triển chủ nghĩa Mác - Lênin, tư tưởng Hồ Chí Minh, quan điểm của Đảng ta về xây dựng quân đội nhân dân cách mạng, chính quy, tinh nhuệ, từng bước hiện đại, về mục tiêu, lý tưởng chiến đấu của quân đội. Đồng thời, vạch trần bản chất chính trị phản động, âm mưu, thủ đoạn “phi chính trị hóa”, “dân sự hóa” quân đội của các thế lực thù địch. Mọi hành động phủ nhận mục tiêu, lý tưởng chiến đấu của quân đội ta đều là nhằm xóa bỏ vai trò lãnh đạo của Đảng Cộng sản Việt Nam đối với quân đội, chuyển hóa lập trường chính trị, bản chất giai cấp công nhân của quân đội phải được nhận diện rõ ràng và đấu tranh để làm thất bại.
Thực tiễn quá trình chiến đấu, trưởng thành của quân đội ta hơn 75 năm qua đã khẳng định: việc giữ vững, tăng cường bản chất giai cấp công nhân, tính nhân dân, tính dân tộc, kiên định mục tiêu, lý tưởng chiến đấu đã làm cho quân đội ta càng đánh càng mạnh, càng đánh càng trưởng thành, thật sự là lực lượng chính trị, lực lượng chiến đấu tuyệt đối trung thành, tin cậy của Đảng, Nhà nước và nhân dân.
<strong>
    <br>II. CHỨC NĂNG, NHIỆM VỤ VÀ ĐỐI TƯỢNG TÁC CHIẾN CỦA QUÂN ĐỘI NHÂN DÂN VIỆT NAM 
<br>1. Chức năng của Quân đội nhân dân Việt Nam 

</strong>
Quân đội ta có 3 chức năng cơ bản: Đội quân chiến đấu; đội quân công tác; đội quân lao động sản xuất. Ba chức năng này là một thể thống nhất, quan hệ chặt chẽ với nhau, phản ánh bản chất cách mạng của quân đội ta. 
Một là, chức năng đội quân chiến đấu là chức năng cơ bản, chủ yếu, phản ánh bản chất cách mạng, quan điểm, đường lối quân sự của Đảng ta, đồng thời là chức năng nổi trội phản ánh tính chất hoạt động quân sự. 
Từ khi ra đời và quá trình xây dựng, chiến đấu, trưởng thành, quân đội ta luôn luôn là công cụ bạo lực sắc bén, lực lượng chính trị tin cậy, lực lượng chiến đấu trung thành của Đảng, Nhà nước và nhân dân; làm tốt vai trò nòng cốt bảo vệ lợi ích của giai cấp công nhân, của nhân dân lao động và toàn thể dân tộc Việt Nam.
Trong cách mạng giải phóng dân tộc, chức năng chiến đấu được thể hiện ở việc chiến đấu chống quân xâm lược và tay sai bán nước, làm nòng cốt cho toàn dân đánh giặc, giành độc lập, tự do cho Tổ quốc.
Ngày nay, chức năng chiến đấu được thể hiện trong nhiệm vụ huấn luyện, chiến đấu, sẵn sàng chiến đấu và chiến đấu thắng lợi trong mọi tình huống, không để bị động, bất ngờ. Quân đội phải luôn chủ động, chuẩn bị tốt mọi mặt đối phó thắng lợi với mọi kẻ thù, mọi hành động xâm phạm lãnh thổ, chủ quyền biên giới, biển đảo, trên không và không gian mạng quốc gia; những tác động của an ninh truyền thống, phi truyền thống, chiến tranh ủy nhiệm, phi quy ước, phi đối xứng; đồng thời, đấu tranh làm thất bại âm mưu, thủ đoạn “diễn biến hòa bình”, khủng bố, can thiệp, lật đổ của các thế lực thù địch trong và ngoài nước.
Hai là, chức năng đội quân công tác là chức năng cơ bản, quan trọng thuộc về bản chất, truyền thống của quân đội ta.
Chức năng đội quân công tác thể hiện ở việc quân đội tham gia công tác trên các lĩnh vực chính trị, kinh tế, văn hoá, xã hội, đối ngoại, góp phần quan trọng vào sự nghiệp xây dựng đất nước, củng cố quốc phòng và bảo vệ Tổ quốc. 
Thực hiện chức năng đội quân công tác, chính là tuyên truyền, vận động nhân dân và cùng với nhân dân thực hiện tốt mọi chủ trương, đường lối của Đảng, chính sách, pháp luật của Nhà nước; tham gia xây dựng hệ thống chính trị vững mạnh, nhất là xây dựng tổ chức đảng, chính quyền, các đoàn thể chính trị - xã hội địa phương; giúp chính quyền và nhân dân phát triển sản xuất, xây dựng cuộc sống ấm no, hạnh phúc; là lực lượng chủ lực trong phòng, chống, khắc phục hậu quả thiên tai, dịch bệnh “chống dịch như chống giặc”, sẵn sàng hy sinh để bảo vệ tính mạng, tài sản của nhân dân, qua đó, tăng cường quan hệ gắn bó máu thịt quân dân.
Trong tình hình hiện nay, quân đội càng phải thực hiện tốt chức năng đội quân công tác, xây dựng “thế trận lòng dân” vững chắc, góp phần giữ vững ổn định chính trị, làm thất bại âm mưu, thủ đoạn “diễn biến hòa bình”, gây rối, bạo loạn lật đổ của các thế lực thù địch; phòng, chống có hiệu quả những biểu hiện “tự diễn biến”, “tự chuyển hóa” trong nội bộ, củng cố niềm tin của nhân dân đối với Đảng, Nhà nước và quân đội, tiếp tục làm tỏa sáng phẩm chất “Bộ đội Cụ Hồ” thời kỳ mới.
Ba là, chức năng đội quân lao động sản xuất là chức năng có ý nghĩa thực tiễn sâu sắc, thuộc về bản chất, truyền thống tốt đẹp của quân đội ta, vừa chiến đấu, vừa sản xuất góp phần nâng cao đời sống bộ đội.
Ngay từ ngày đầu thành lập và trong suốt chặng đường chiến đấu và trưởng thành, quân đội ta luôn luôn tự lực, tự cường lao động, sản xuất làm ra lương thực, thực phẩm để nuôi quân; sản xuất, cải tiến vũ khí, trang bị kỹ thuật quân sự, giảm bớt một phần cung cấp của Nhà nước, của nhân dân cho quân đội, góp phần tạo nên sức mạnh vô địch, đánh thắng kẻ thù xâm lược. 
Quân đội ta là lực lượng có tiềm năng đáng kể về sức người, cơ sở vật chất, trang bị kỹ thuật, được tổ chức chặt chẽ, có tác phong chính quy, có trình độ để ứng dụng các tiến bộ khoa học, công nghệ vào sản xuất; những địa bàn khó khăn có bộ đội. Đây là những điều kiện thuận lợi để quân đội tham gia lao động sản xuất, phát triển kinh tế. Trong sự nghiệp xây dựng và bảo vệ Tổ quốc, quân đội tích cực lao động sản xuất, góp phần vào việc giảm bớt gánh nặng kinh tế cho đất nước, tích cực củng cố tiềm lực kinh tế, đồng thời, cũng là cơ sở để xây dựng quân đội cách mạng, chính quy, tinh nhuệ, từng bước hiện đại.
Trong giai đoạn hiện nay, thực hiện chức năng lao động sản xuất, quân đội phải quán triệt và vận dụng quan điểm, đường lối phát triển kinh tế của Đảng. Kết hợp chặt chẽ phát triển kinh tế, văn hóa, xã hội với củng cố, tăng cường quốc phòng, an ninh, sẵn sàng chiến đấu, đẩy mạnh tăng gia sản xuất, nâng cao đời sống của bộ đội, thực hành tiết kiệm, chống lãng phí. 
Ba chức năng trên có quan hệ thống nhất, tác động hỗ trợ cho nhau, trong đó, chức năng chiến đấu là chức năng cơ bản, quan trọng nhất. 
<strong><br>2. Nhiệm vụ của quân đội
</strong>
<br>Hiện nay, quân đội ta có 5 nhiệm vụ cơ bản:
<br>Một là, sẵn sàng chiến đấu và chiến đấu thắng lợi.
<br>Đây là nhiệm vụ chính trị hàng đầu, có ý nghĩa quyết định trực tiếp đến việc thực hiện chức năng chiến đấu của quân đội ta. Để bảo đảm cho quân đội sẵn sàng chiến đấu và chiến đấu thắng lợi phải quán triệt, nắm vững mục tiêu, nhiệm vụ bảo vệ Tổ quốc trong tình hình mới được Đại hội lần thứ XIII của Đảng xác định là: “Bảo vệ vững chắc độc lập, chủ quyền, thống nhất, toàn vẹn lãnh thổ của Tổ quốc, bảo vệ Đảng, Nhà nước, nhân dân, chế độ xã hội chủ nghĩa, nền văn hoá và lợi ích quốc gia - dân tộc; giữ vững môi trường hòa bình, ổn định chính trị, an ninh quốc gia, an ninh con người, xây dựng xã hội trật tự, kỷ cương, an toàn, lành mạnh để phát triển đất nước theo định hướng xã hội chủ nghĩa” . Phải coi an ninh, an toàn là một trong những yếu tố hàng đầu trong cuộc sống của người dân. Ứng phó kịp thời, hiệu quả với các mối đe dọa an ninh phi truyền thống, nhất là nhiệm vụ cứu nạn, cứu hộ, phòng, chống thiên tai, dịch bệnh. Kiên quyết, kiên trì bảo vệ vững chắc độc lập, chủ quyền, thống nhất, toàn vẹn lãnh thổ, vùng trời, vùng biển, đảo của Tổ quốc.
<br>Trong đó, cần xây dựng quân đội cách mạng, chính quy, tinh nhuệ, từng bước hiện đại; lấy vững mạnh về chính trị và chuẩn mực “Bộ đội Cụ Hồ” làm cơ sở để nâng cao chất lượng tổng hợp, sức mạnh, trình độ sẵn sàng chiến đấu của quân đội, đáp ứng ngày càng tốt hơn yêu cầu, nhiệm vụ bảo vệ Tổ quốc trong tình hình mới. Tích cực điều chỉnh tổ chức quân đội theo hướng “tinh, gọn, mạnh”; ưu tiên đầu tư cho một số quân, binh chủng, lực lượng tiến thẳng lên hiện đại. Đồng thời, duy trì nghiêm các chế độ sẵn sàng chiến đấu; thường xuyên nắm chắc diễn biến tình hình trên bộ, trên biển, trên không và trên không gian mạng, nhất là ở các tuyến biên giới đất liền, trên Biển Đông, trên không gian mạng, phát hiện kịp thời âm mưu, thủ đoạn phá hoại của các thế lực thù địch, hành động xâm phạm lãnh thổ, chủ quyền, lợi ích quốc gia, dân tộc; bổ sung hoàn chỉnh các phương án chiến đấu, lấy chủ động phòng ngừa là chính; khi có tình huống xảy ra phải xử lý kịp thời, kiên quyết, kiên trì, khôn khéo, nhanh gọn, không để lan rộng, không để các thế lực thù địch lấy cớ can thiệp; quá trình xử lý phải tập trung vào địa bàn trọng điểm, tình huống chủ yếu. Các đơn vị phải duy trì nghiêm chế độ trực ban, trực chỉ huy, trực sẵn sàng chiến đấu theo quy định. Mỗi quân nhân phải không ngừng nâng cao cảnh giác, ý thức và trình độ sẵn sàng chiến đấu, bảo đảm chiến đấu thắng lợi trong mọi tình huống.
<br>Hai là, huấn luyện, xây dựng quân đội, đơn vị vững mạnh toàn diện “Mẫu mực, tiêu biểu”.
Đây là nhiệm vụ trọng tâm, thường xuyên, tác động trực tiếp đến nâng cao trình độ sẵn sàng chiến đấu, khả năng, hiệu quả chiến đấu của quân đội.
Huấn luyện phải đảm bảo thường xuyên và toàn diện, quán triệt, thực hiện đúng phương châm “Cơ bản, thiết thực, vững chắc”, vận dụng tốt 3 quan điểm, 8 nguyên tắc, 6 mối kết hợp trong huấn luyện, phù hợp với từng đối tượng, đặc điểm tình hình địa bàn và sát với thực tế chiến đấu ; đảm bảo sát thực tiễn, nhiệm vụ, phù hợp đối tượng, địa bàn tác chiến; gắn huấn luyện với hoàn thành tốt các nhiệm vụ được giao của đơn vị.
Nội dung huấn luyện, sẵn sàng chiến đấu tập trung vào việc nâng cao nhận thức, xây dựng bản lĩnh chính trị, rèn luyện tâm lý, sức khỏe, ý thức tổ chức kỷ luật, tinh thần đoàn kết; thuần thục kỹ thuật, chiến thuật; sử dụng thành thạo và có hiệu quả vũ khí, trang bị kỹ thuật quân sự, nâng cao sức cơ động, sẵn sàng chiến đấu cao, đủ sức đánh thắng kẻ thù trong mọi tình huống.
Tùy theo nội dung, điều kiện đơn vị để lựa chọn các hình thức, phương pháp tổ chức huấn luyện phù hợp. Trước hết, mỗi cán bộ, chiến sĩ phải coi đây là nhiệm vụ trọng tâm, tích cực tự giác, thường xuyên học tập, rèn luyện nâng cao bản lĩnh chính trị, trình độ kỹ, chiến thuật và rèn luyện sức khỏe đáp ứng yêu cầu nhiệm vụ được giao.
Thường xuyên quán triệt, nắm vững và thực hiện tốt các nội dung, biện pháp xây dựng đơn vị vững mạnh toàn diện “Mẫu mực, tiêu biểu” trên 5 tiêu chuẩn, 4 nội dung: Vững mạnh về chính trị; tổ chức biên chế đúng quy định, huấn luyện giỏi, khả năng sẵn sàng chiến đấu cao, hoàn thành tốt nhiệm vụ chính trị và các nhiệm vụ khác; xây dựng nền nếp chính quy và quản lý kỷ luật tốt; thực hiện tốt công tác hậu cần, tài chính, kế hoạch và đầu tư; bảo đảm tốt về công tác kỹ thuật. Đẩy mạnh phong trào thi đua quyết thắng gắn với đẩy mạnh học tập và làm theo tư tưởng, đạo đức, phong cách Hồ Chí Minh, Cuộc vận động phát huy truyền thống, cống hiến tài năng, xứng danh “Bộ đội Cụ Hồ” thời kỳ mới; chấp hành nghiêm pháp luật Nhà nước, kỷ luật quân đội.
Ba là, tích cực tham gia lao động sản xuất, góp phần phát triển kinh tế, văn hóa, xã hội, cải thiện đời sống bộ đội.
Đây là nhiệm vụ quan trọng phản ánh quy luật bảo vệ Tổ quốc phải đi đôi với xây dựng đất nước, thực hiện thắng lợi hai nhiệm vụ chiến lược xây dựng và bảo vệ Tổ quốc. Trong Báo cáo chính trị tại Đại hội lần thứ XIII, Đảng ta xác định chủ trương: “Kết hợp chặt chẽ, hiệu quả giữa kinh tế, văn hoá, xã hội, đối ngoại với quốc phòng, an ninh và giữa quốc phòng, an ninh với kinh tế, văn hoá, xã hội và đối ngoại”  nhằm thực hiện thắng lợi nhiệm vụ bảo vệ Tổ quốc trong tình hình mới. Do đó, cùng với nhiệm vụ huấn luyện, sẵn sàng chiến đấu, xây dựng đơn vị, phải đồng thời làm tốt nhiệm vụ tham gia lao động sản xuất.
Trong tham gia xây dựng kinh tế, các doanh nghiệp quân đội phải nắm  vững quan điểm của Đảng về phát triển kinh tế thị trường định hướng xã hội chủ nghĩa, chủ động và tích cực hội nhập quốc tế, sản xuất kinh doanh có hiệu quả, cạnh tranh lành mạnh, chấp hành nghiêm pháp luật, nâng cao chất lượng sản phẩm, phấn đấu trở thành những doanh nghiệp mạnh, tiêu biểu, góp phần giữ vững định hướng xã hội chủ nghĩa, phát triển kinh tế nhanh, bền vững. 
Đối với các đơn vị công nghiệp quốc phòng phải tiếp tục quán triệt và thực hiện các mục tiêu, chỉ tiêu của Nghị quyết 06-NQ/TW của Bộ Chính trị khóa XI về xây dựng và phát triển công nghiệp quốc phòng đến năm 2020 và những năm tiếp theo. Chủ động rà soát, xây dựng quy hoạch, kế hoạch phát triển công nghiệp quốc phòng phù hợp với tình hình thực tiễn. Tích cực huy động các nguồn lực, nâng cao chất lượng đầu tư, tập trung thực hiện tốt các dự án, nhất là các dự án trọng điểm; gắn kết chặt chẽ giữa sản xuất hàng hóa quân sự với hàng hóa dân dụng, góp phần phát triển kinh tế đất nước, khi cần có thể chuyển ngay sang sản xuất quốc phòng.
Các đơn vị huấn luyện, sẵn sàng chiến đấu cần phát huy tính tích cực, chủ động khắc phục khó khăn, tăng gia sản xuất, thực hành tiết kiệm, chống lãng phí, góp phần nâng cao đời sống bộ đội và tham gia làm tốt chính sách hậu phương quân đội.
<br>Bốn là, thực hiện tốt công tác tuyên truyền, vận động nhân dân.
<br>Đây là nhiệm vụ quan trọng, thuộc về bản chất cách mạng, truyền thống tốt đẹp của quân đội ta nhằm góp phần tăng cường sức mạnh đoàn kết toàn dân tộc, củng cố, tăng cường mối quan hệ giữa Đảng, Nhà nước với nhân dân, nhân dân với quân đội. 
 <br>Tuyên truyền cho nhân dân nhận thức sâu sắc quan điểm, chủ trương, chính sách của Đảng, pháp luật của Nhà nước, củng cố “thế trận lòng dân” vững chắc, tham gia xây dựng cơ sở chính trị địa phương, giúp dân phát triển kinh tế, văn hóa, xã hội, xóa đói, giảm nghèo; phòng, chống thiên tai, lũ lụt, dịch bệnh. Đồng thời, tuyên truyền cho nhân dân hiểu rõ âm mưu, thủ đoạn của kẻ thù, đề cao cảnh giác đấu tranh chống âm mưu, thủ đoạn “diễn biến hòa bình”, bạo loạn lật đổ, những quan điểm, tư tưởng, luận điệu sai trái của các thế lực thù địch, những phần tử cơ hội, phản động.
<br>Năm là, củng cố, xây dựng và phát triển quan hệ đoàn kết hữu nghị với nhân dân và quân đội các nước vì hoà bình, độc lập dân tộc, dân chủ và chủ nghĩa xã hội.
<br>Đây là nhiệm vụ chính trị phản ánh truyền thống tốt đẹp, bản chất cách mạng của quân đội ta, kết hợp sức mạnh dân tộc với sức mạnh thời đại, tranh thủ tối đa sự đồng tình ủng hộ của cộng đồng quốc tế trong sự nghiệp xây dựng và bảo vệ Tổ quốc Việt Nam xã hội chủ nghĩa. Quân đội phải thường xuyên quán triệt, thấu suốt đường lối, quan điểm, chính sách đối ngoại của Đảng và Nhà nước ta; công tác hội nhập quốc tế, đối ngoại quốc phòng cần đổi mới, sáng tạo, đi vào chiều sâu, cả song phương và đa phương; là một trong những trụ cột đối ngoại của Đảng, ngoại giao Nhà nước và đối ngoại nhân dân, góp phần bảo vệ Tổ quốc từ sớm, từ xa; làm sâu sắc thêm quan hệ với các nước láng giềng, cân bằng quan hệ với các nước lớn và giữ vững quan hệ với các nước bạn bè truyền thống. Chủ động tham gia các diễn đàn quân sự, hội thao quân sự, quốc phòng đa phương; phối hợp, tổ chức giao lưu hữu nghị quốc phòng biên giới; tích cực tham gia lực lượng gìn giữ hòa bình của Liên hợp quốc, góp phần nâng cao uy tín, vị thế của nước ta trên trường quốc tế, nhất là trong khối ASEAN.
<br>Năm nhiệm vụ trên, mỗi nhiệm vụ có vị trí và yêu cầu khác nhau, nhưng có mối quan hệ chặt chẽ, thống nhất, không coi nhẹ bất cứ nhiệm vụ nào. Trong đó, nhiệm vụ sẵn sàng chiến đấu và chiến đấu thắng lợi là nhiệm vụ chính trị hàng đầu của quân đội ta. 
<strong><br>3. Đối tượng tác chiến của quân đội ta hiện nay
</strong>
<br>Bất cứ lực lượng nào chống phá mục tiêu của cách mạng nước ta; tiến hành chiến tranh xâm lược; “diễn biến hòa bình”, bạo loạn lật đổ, bạo loạn vũ trang ở nước ta; xâm phạm độc lập, chủ quyền, thống nhất, toàn vẹn lãnh thổ, lợi ích quốc gia, dân tộc của Việt Nam thì lực lượng đó là đối tượng tác chiến của quân đội ta. 
<br>Có thể phân thành ba nhóm đối tượng tác chiến sau đây:
<br>Nhóm thứ nhất: Bất cứ thế lực nào có âm mưu và hành động tiến hành chiến tranh xâm lược nước ta dưới bất kỳ hình thức nào. Dấu hiệu nhận biết là đối tượng có các hành động chuẩn bị và tiến hành chiến tranh dưới mọi hình thức, xúc tiến liên minh, liên kết tập hợp lực lượng tiến công ta bằng vũ lực.
<br>Nhóm thứ hai: Bất cứ thế lực nào có âm mưu và hành động thực hiện “diễn biến hòa bình”, gây bạo loạn lật đổ, bạo loạn vũ trang ở nước ta. Dấu hiệu nhận biết là chúng sử dụng đòn tiến công chính bằng “diễn biến hòa bình”, chống phá mục tiêu xây dựng chủ nghĩa xã hội; thực hiện “phi chính trị hóa”, “dân sự hóa” quân đội, thúc đẩy “tự diễn biến”, “tự chuyển hóa” trong nội bộ để thay đổi chế độ ta. Khi có điều kiện, thời cơ, nhất là lợi dụng lúc ta có sơ hở trong xử lý các tình huống chiến lược, chúng sẽ kích động gây bạo loạn lật đổ, xung đột vũ trang hoặc chiến tranh với các dạng thức mới như: chiến tranh ủy nhiệm, chiến tranh thông tin, chiến tranh mạng…
<br>Nhóm thứ ba: Bất cứ thế lực nào có âm mưu và hành động xâm phạm độc lập, chủ quyền, thống nhất, toàn vẹn lãnh thổ của Việt Nam, xâm phạm lợi ích quốc gia, dân tộc bằng phương thức vũ trang hoặc phi vũ trang, kể cả từ bên trong và bên ngoài. Dấu hiệu nhận biết là đối tượng thực hiện ý đồ tranh đoạt chủ quyền lãnh thổ của Việt Nam bằng các hoạt động xâm lấn biên giới, biển, đảo thuộc chủ quyền lãnh thổ của ta. Họ tìm mọi cách kiềm chế, chi phối chính sách đối nội, đối ngoại, buộc ta phải nhượng bộ về chủ quyền, lợi ích quốc gia. Khi có thời cơ, có thể gây xung đột vũ trang, chiến tranh chiếm giữ lãnh thổ, chiếm đoạt tài nguyên của nước ta.
<strong><br>4. Trách nhiệm của mỗi chiến sĩ trong việc giữ vững, tăng cường bản chất giai cấp công nhân, kiên định mục tiêu, lý tưởng chiến đấu, thực hiện thắng lợi nhiệm vụ được giao
</strong>
<br>- Ra sức học tập và tu dưỡng nâng cao giác ngộ về chính trị, tư tưởng, quan điểm, lập trường của giai cấp công nhân. Kiên định mục tiêu độc lập dân tộc và chủ nghĩa xã hội. Tuyệt đối trung thành, tin tưởng vào sự lãnh đạo của Đảng, sẵn sàng chiến đấu hy sinh, vì độc lập, tự do của Tổ quốc, vì chủ nghĩa xã hội, bảo vệ Đảng, Nhà nước, chế độ xã hội chủ nghĩa, bảo vệ nhân dân. Nắm vững và thực hiện nghiêm mọi chủ trương, nghị quyết, chỉ thị của Đảng, pháp luật của Nhà nước. Tích cực tham gia xây dựng các tổ chức đảng trong sạch vững mạnh, đơn vị vững mạnh toàn diện. Đề cao tự phê bình và phê bình, tích cực đóng góp ý kiến xây dựng cho cán bộ, đảng viên. Không ngừng phấn đấu để xứng đáng là đoàn viên ưu tú, được đứng vào hàng ngũ của Đảng.
<br>- Tích cực rèn luyện, học tập, lao động, nâng cao trình độ kỹ, chiến thuật, làm chủ vũ khí trang bị, sẵn sàng chiến đấu cao, hoàn thành tốt chức trách, nhiệm vụ được giao, góp phần xây dựng đơn vị vững mạnh toàn diện.
<br>- Đề cao ý thức tổ chức kỷ luật, rèn luyện tác phong chính quy, chấp hành nghiêm pháp luật của Nhà nước, điều lệnh, điều lệ của quân đội, thương yêu đồng chí, kỷ luật nghiêm minh. Tự giác tu dưỡng phẩm chất, đạo đức, lối sống; đấu tranh, phê phán các quan điểm, hành động sai trái, tiêu cực, vi phạm kỷ luật, suy thoái về chính trị tư tưởng, đạo đức, lối sống.
<br>- Luôn nêu cao tinh thần cảnh giác cách mạng, không mơ hồ, ảo tưởng. Kiên quyết đấu tranh làm thất bại âm mưu, thủ đoạn “diễn biến hòa bình”, gây bạo loạn lật đổ của các thế lực thù địch; phê phán những tư tưởng và hành động sai trái, thù địch, bảo vệ quan điểm, đường lối của Đảng, chính sách, pháp luật của Nhà nước.
<br>- Tích cực tham gia tuyên truyền, vận động nhân dân và gia đình thực hiện đường lối, chủ trương của Đảng, chính sách và pháp luật của Nhà nước, giúp nhân dân phát triển sản xuất, xóa đói giảm, nghèo, tham gia xây dựng cơ sở chính trị địa phương nơi đóng quân, tăng cường mối quan hệ máu thịt giữa quân đội và nhân dân.
<br> - Tích cực tăng gia sản xuất, triệt để thực hành tiết kiệm, giữ tốt, dùng bền các cơ sở vật chất, trang thiết bị của đơn vị; đồng thời, kiên quyết đấu tranh chống mọi biểu hiện tham ô, tham nhũng, lãng phí trong đơn vị.



